.search {
  display: flex;
  margin: 15px 0;
}

.search__input {
  font-size: 16px;
  height: 40px;
  background-color: #0d1117;
  color: #E6EDF3;
  outline: none;
  border: 1px solid #21262d;
  padding: 5px 15px;
  border-radius: 5px;
}

.search__input-wrapper {
  position: relative;
  min-width: 250px;
  max-width: 300px;
}

.search__icon {
  color: #E6EDF3;
  font-size: 16px;
}

.search__button-close {
  position: absolute;
  right: 35px;
  top: 12px;
  background-color: transparent;
  border: none;
  outline: none;
}

.search__button-submit {
  font-size: 16px;
  height: 40px;
  background-color: #0d1117;
  color: #E6EDF3;
  outline: none;
  border: 1px solid #21262d;
  padding: 5px 15px;
  border-radius: 5px;
  font-weight: 500;
  outline: none;
}
